.section__title {
  position: relative;
  font-size: 36px;
  font-weight: 700;
  text-align: center;
}
.section__title:before {
  content: '';
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  width: 170px;
  height: 8px;
  background: #fff200;
  box-shadow: 8.485px 8.485px 15.81px 1.19px rgba(0, 0, 0, 0.27);
}
::-webkit-input-placeholder {
  font-size: 16px;
}
::-moz-placeholder {
  font-size: 16px;
}
:-ms-input-placeholder {
  font-size: 16px;
}
:-moz-placeholder {
  font-size: 16px;
}
.header {
  overflow: hidden;
}
.header .container {
  position: relative;
}
.header .container:before {
  content: '';
  position: absolute;
  top: 0;
  right: -990px;
  height: 100%;
  width: 1000px;
  background: #000;
}
.header__box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.header__logo {
  flex-basis: 350px;
}
.header__nav {
  flex-basis: 520px;
}
.header .nav__list {
  display: flex;
  align-items: center;
}
.header .nav__item {
  list-style-type: none;
}
.header .nav__link {
  display: block;
  padding: 7px 11px;
  line-height: 60px;
  font-size: 19.56px;
  transition: 0.3s linear background-color;
}
.header .nav__link:hover {
  background: #fff200;
}
.header__time {
  font-weight: 500;
}
.header__btn {
  position: relative;
  width: 100px;
  height: 74px;
  background: #fff200;
  overflow: hidden;
}
.header__btn:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 32px;
  height: 54px;
  background: url('../images/icons/sprites.png') -201px -107px;
}
.header__btn:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: -4.5em;
  height: 100%;
  width: 3em;
  transform: skewX(-45deg) translateX(0);
  transition: none;
  background-color: rgba(255, 255, 255, 0.5);
}
.header__btn:hover {
  color: #fff;
}
.header__btn:hover:after {
  transform: skewX(-45deg) translateX(13.5em);
  transition: all .5s linear;
}
.headerMain {
  padding-top: 285px;
  background: url(../images/hero__back.jpg) no-repeat center / cover;
  text-align: center;
  font-weight: 700;
}
.headerMain__title {
  font-size: 48px;
  color: #fff;
}
.headerMain__title strong {
  font-size: 122px;
  color: #fff200;
}
.headerMain__address {
  padding-top: 40px;
  font-size: 50px;
  color: #fff200;
}
.headerMain .scrollMore {
  position: relative;
  display: inline-block;
  margin: 180px auto -5px auto;
  width: 76px;
  height: 100px;
  background: #fff200;
  text-align: center;
}
.headerMain .scrollMore:before {
  content: '';
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 13px;
  height: 18px;
  background: url('../images/icons/sprites.png') -319px -209px;
}
