.sale {
  position: relative;
  background: url(../images/sale__back.jpg) no-repeat center / cover;
  box-shadow: 8.485px 8.485px 15.81px 1.19px rgba(0, 0, 0, 0.27);
}
.sale:before {
  content: '';
  position: absolute;
  top: -5px;
  left: 0;
  width: 100%;
  box-shadow: 8.485px 8.485px 15.81px 1.19px #000000;
}
.sale__box {
  display: flex;
  justify-content: space-between;
  padding: 100px 0;
}
.sale__item {
  position: relative;
  list-style-type: none;
  box-shadow: 8.485px 8.485px 15.81px 1.19px rgba(0, 0, 0, 0.27);
  transition: box-shadow 0.3s linear;
}
.sale__item:hover {
  box-shadow: 8.485px 8.485px 15.81px 1.19px rgba(0, 0, 0, 0.5);
}
.sale__link {
  display: block;
}
.sale__link:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}
.sale__link:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  border: 2px solid #afa7a0;
  transition: all .3s linear;
}
.sale__link:hover .sale__content {
  opacity: 0;
}
.sale__link:hover .sale__hover {
  opacity: 1;
}
.sale__link:hover:after {
  width: 95%;
  height: 95%;
}
.sale__img {
  display: block;
  max-width: 368px;
  height: 276px;
}
.sale__content {
  font-weight: 700;
  transition: all .3s linear;
}
.sale__product {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 48px;
  color: #fff;
}
.sale__percent {
  display: block;
  padding: 12px 0;
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 0;
  font-size: 30px;
  color: #fff200;
  background: #000;
}
.sale__hover {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  opacity: 0;
  transition: all .3s linear;
  font-size: 30px;
  font-weight: 700;
  text-align: center;
  color: #fff;
}
.catalogPlus {
  padding-bottom: 0;
  position: relative;
  overflow: hidden;
  box-shadow: 8.485px 8.485px 15.81px 1.19px rgba(0, 0, 0, 0.27);
}
.catalogPlus:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 270px;
  background-image: -moz-linear-gradient(90deg, #cccccc 0%, rgba(225, 229, 235, 0) 100%);
  background-image: -webkit-linear-gradient(90deg, #cccccc 0%, rgba(225, 229, 235, 0) 100%);
  background-image: -ms-linear-gradient(90deg, #cccccc 0%, rgba(225, 229, 235, 0) 100%);
}
.catalogPlus__box {
  margin-top: 80px;
  display: flex;
  justify-content: center;
  height: 560px;
  overflow: hidden;
  box-shadow: 8.485px 8.485px 15.81px 1.19px rgba(0, 0, 0, 0.27);
}
.catalogPlus__link {
  display: block;
  position: relative;
  outline: none!important;
  transition: all .3s linear;
}
.catalogPlus__link:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 95%;
  height: 95%;
  border: 2px solid #969596;
  transition: all .3s linear;
}
.catalogPlus__link:hover:before {
  width: 90%;
  height: 90%;
}
.catalogPlus__img {
  display: block;
  width: 380px;
  height: auto;
}
.brand {
  background: url(../images/brands__back.jpg) no-repeat center / cover;
  padding-top: 100px;
  padding-bottom: 100px;
}
.brand__list {
  padding-top: 70px;
}
.brand__item {
  position: relative;
  list-style-type: none;
  margin-right: 15px;
  background: #fff;
}
.brand__wrap {
  display: flex;
  align-items: center;
  height: 100px;
  outline: none!important;
  cursor: pointer;
}
.brand__img {
  display: block;
  max-width: 150px;
  height: auto;
  margin: auto;
}
.brandHistory {
  text-align: left;
}
.brandHistory__text {
  padding-bottom: 15px;
  color: #616d74;
}
.brandHistory__img {
  display: block;
  margin: auto;
  margin-bottom: 30px;
}
