.burger {
  position: relative;
  width: 50px;
  height: 50px;
  transition: all 0.6s ease-in-out;
  z-index: 1000;
  background: transparent;
  border: none;
  outline: none;
  display: block;
  z-index: 100;
}
.burger:hover {
  cursor: pointer;
}
@media only screen and (min-width: 992px) {
  .burger {
    display: none;
  }
}
.burger i {
  display: block;
  width: 40px;
  height: 2px;
  background: #fff200;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}
.burger i:before,
.burger i:after {
  content: '';
  position: absolute;
  height: 2px;
  background: #fff200;
  width: 40px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s ease-in-out;
}
.burger i:before {
  margin-top: -12px;
}
.burger i:after {
  margin-top: 12px;
}
.burger:hover i:before {
  margin-top: -14px;
}
.burger:hover i:after {
  margin-top: 14px;
}
.burger.active i {
  background: transparent;
}
.burger.active i:before,
.burger.active i:after {
  left: -5px;
  width: 50px;
}
.burger.active i:before {
  transform: rotate(45deg);
  top: 12px;
}
.burger.active i:after {
  transform: rotate(-45deg);
  top: -12px;
}
.burger.active:hover i:before,
.burger.active:hover i:after {
  width: 60px;
  margin: 0;
  left: -10px;
  top: 0;
}
